.dialog_item {
	&:hover {
		background: #6b6e75;
	}
	.dialog_user_name {
		padding: 10px 15px;
		display: flex;
		color: white;
		column-gap: 15px;
		align-items: center;
		.user_name {
			line-height: 18px;
		}
	}
	.active_dialog {
		background: #6b6e75;
	}
}
.friend_picture {
	.friend_img {
		width: 50px;
		height: 50px;
		border-radius: 50px;
	}
}
